import { getAPI } from '@/api/api.js';
export default {
  data: function data() {
    return {
      current: 0,
      size: 10,
      shopList: [],
      value: [],
      loading: false,
      finished: false,
      total: 0
    };
  },
  methods: {
    getDate: function getDate() {
      var _this = this;

      getAPI('/get_store', {
        current: this.current,
        size: this.size
      }).then(function (res) {
        // 把评分放到双向绑定中的value并优化成0,0.5,1形式
        for (var i = 0; i < res.data.list.length; i++) {
          var num = res.data.list[i].score.substring(2, 4);

          if (Number(num) > 5) {
            _this.value = _this.value.concat(Math.ceil(res.data.list[i].score));
          } else if (Number(num) < 5 && Number(num) > 0) {
            _this.value = _this.value.concat(Number(parseInt(res.data.list[i].score) + 0.5));
          } else {
            _this.value = _this.value.concat(Number(res.data.list[i].score));
          }
        }

        _this.loading = false;
        _this.current++;
        _this.total = res.data.total;
        _this.shopList = _this.shopList.concat(res.data.list);

        if (_this.shopList.length >= _this.total) {
          _this.finished = true;
        }
      });
    }
  } // created(){
  // 	this.getDate();
  // }

};